@use "variables" as *;

.btn,
.btn-success {
  font-size: 2rem;
}

.Main-form-container {
  width: 45vw;
  height: 55px;
  background-color: $primary-color;
  position: relative;

  .Main-greetForm {
    input[type="text"] {
      height: 100%;
      width: 75%;
      position: absolute;
      top: 0;
      left: 0;
      padding-left: 5%;
      font-size: 2rem;
      letter-spacing: 2.8px;
      color: $input-color;
      border: none;
      &:focus {
        outline: none;
        background-color: $input-focus;
      }
    }

    button[type="submit"] {
      height: 100%;
      width: 25%;
      position: absolute;
      right: 0;
      top: 0;
      span {
        position: relative;
        top: -7px;
        left: 8px;
        padding-right: 0.9rem;
      }
    }
  }
}
